import React, { Component } from "react";
import IntlMessages from "../../../../components/utility/intlMessages";
import getLanguage from "../../../App/MyLanguange";
import { Form, Input, Col, Checkbox, Modal } from "antd";

const InputGroup = Input.Group;
const FormItem = Form.Item;

class ModalAddLocationType extends Component {
  state = {
    check_code: false
  };

  componentDidUpdate = () => {
    const { resetFields } = this.props.form;
    const { success } = this.props.locationType;
    if (success) {
      resetFields();
      this.setState({
        check_code: false
      });
    }
  };

  handleChangeCode = () => {
    const { setFieldsValue } = this.props.form;
    this.setState({ check_code: !this.state.check_code });
    if (this.state.check_code === false) {
      setFieldsValue({
        ["location_type_code"]: null
      });
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { locationType } = this.props;
    const { loading } = locationType;
    const error = locationType.errorField ? locationType.errorField : [];

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    return (
      <Modal
        title={<IntlMessages id="locationTypes.button.add" />}
        visible={this.props.visible}
        centered
        onCancel={this.props.handleModal}
        onOk={this.handleSubmit}
        width="40%"
        okText={getLanguage({ id: "button.submit" })}
        cancelText={getLanguage({ id: "button.cancel" })}
        okButtonProps={{ loading: loading }}
        cancelButtonProps={{ loading: loading }}
      >
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          <div className="isoDetailDataFlex" style={{ padding: "0" }}>
            <div
              className="isoColumn-2 isoFormGroupComp"
              style={{ padding: "0 15px 0 0" }}
            >
              <InputGroup compact>
                <Col span={20}>
                  <FormItem
                    {...formItemLayout}
                    label={<IntlMessages id="locationTypes.add.code" />}
                    validateStatus={
                      error.location_type_code ||
                      getFieldError("location_type_code")
                        ? "error"
                        : null
                    }
                    help={
                      error.location_type_code
                        ? error.location_type_code
                        : getFieldError("location_type_code")
                    }
                  >
                    {getFieldDecorator("location_type_code", {
                      rules: [
                        {
                          max: 30,
                          message: <IntlMessages id="maxLength30" />
                        }
                      ]
                    })(
                      <Input
                        disabled={this.state.check_code ? true : false}
                        //style={{ textTransform: "uppercase" }}
                      />
                    )}
                  </FormItem>
                </Col>
                <Col span={4}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="autoGenerateCode" />}
                  >
                    <Checkbox
                      style={{
                        paddingTop: "5px",
                        paddingLeft: "8px"
                      }}
                      checked={this.state.check_code}
                      onChange={this.handleChangeCode}
                    />
                  </Form.Item>
                </Col>
              </InputGroup>

              <FormItem
                {...formItemLayout}
                label={<IntlMessages id="locationTypes.add.name" />}
              >
                {getFieldDecorator("location_type_name", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    },
                    {
                      max: 50,
                      message: <IntlMessages id="maxLength50" />
                    }
                  ]
                })(
                  <Input
                  //disabled={used ? true : false}
                  />
                )}
              </FormItem>
            </div>
          </div>
        </Form>
      </Modal>
    );
  }
}

const WrappedModalAddLocationType = Form.create({
  name: "modal_add_location_type"
})(ModalAddLocationType);

export default WrappedModalAddLocationType;
