import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import LocationCreateForm from "./createForm";

import notification from "../../../../components/notification";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import locationActions from "../../../../redux/companyMaster/location/actions";
import siteActions from "../../../../redux/companyMaster/site/actions";
import locationTypeActions from "../../../../redux/companyMaster/locationType/actions";
import { Box } from "../../../App/app.style";

const redirectTo = "/dashboard/companies/locations";
const { storeSite } = siteActions;
const { storeLocationType } = locationTypeActions;

class CreateLocation extends Component {
  state = {
    visibleAddSite: false,
    visibleAddLocationType: false
  };
  // Get All data diawal
  componentDidMount() {
    const { createLocation } = this.props;
    createLocation();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.location.error) {
      notification("error", this.props.location.error);
      this.props.clearError();
    }

    if (this.props.location.success) {
      this.props.history.replace(redirectTo);
    }

    // Site
    if (this.props.site.error) {
      notification("error", this.props.site.error);
      this.props.clearError();
    }

    if (this.props.site.success) {
      const { createLocation, clearSuccess } = this.props;
      notification("success", this.props.site.success);
      this.handleModalAddSite();
      clearSuccess();
      createLocation();
    }

    // Location Type
    if (this.props.locationType.error) {
      notification("error", this.props.locationType.error);
      this.props.clearError();
    }

    if (this.props.locationType.success) {
      const { createLocation, clearSuccess } = this.props;
      notification("success", this.props.locationType.success);
      this.handleModalAddLocationType();
      clearSuccess();
      createLocation();
    }
  }

  handleModalAddSite = () => {
    this.setState({
      visibleAddSite: !this.state.visibleAddSite
    });
  };

  handleModalAddLocationType = () => {
    this.setState({
      visibleAddLocationType: !this.state.visibleAddLocationType
    });
  };

  handleSubmit = value => {
    const { storeLocation } = this.props;
    storeLocation(value);
  };

  handleSubmitSite = value => {
    const { storeSite } = this.props;
    storeSite(value);
  };
  handleSubmitLocationType = value => {
    const { storeLocationType } = this.props;
    storeLocationType(value);
  };

  render() {
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="locations.add.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <LocationCreateForm
            auth={this.props.auth}
            submit={this.handleSubmit}
            location={this.props.location}
            site={this.props.site}
            visibleAddSite={this.state.visibleAddSite}
            handleModalAddSite={this.handleModalAddSite}
            handleSubmitSite={this.handleSubmitSite}
            locationType={this.props.locationType}
            visibleAddLocationType={this.state.visibleAddLocationType}
            handleModalAddLocationType={this.handleModalAddLocationType}
            handleSubmitLocationType={this.handleSubmitLocationType}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    location: state.location,
    site: state.site,
    locationType: state.locationType
  };
};

export default compose(
  connect(mapStateToProps, { ...locationActions, storeSite, storeLocationType })
)(CreateLocation);
