import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Select, Radio, Col, Checkbox, InputNumber } from "antd";
import getLanguage from "../../../App/MyLanguange";
import ModalAddSite from "./modalAddSite";
import ModalAddLocationType from "./modalAddLocationType";

const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;

class LocationCreateForm extends Component {
  state = {
    check_code: false
  };

  handleChangeCode = () => {
    const { setFieldsValue } = this.props.form;
    this.setState({ check_code: !this.state.check_code });
    if (this.state.check_code === false) {
      setFieldsValue({
        ["location_code"]: null
      });
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    // console.log(this.props);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { location } = this.props;
    const { loading, loadingSubmit } = location;

    const error = location.errorField ? location.errorField : [];

    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };

    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    const radioGroupStyle = {
      display: "flex",
      width: "50%"
    };

    const radioButtonStyle = {
      flex: "1",
      textAlign: "center"
    };

    return (
      <>
        <ModalAddSite
          handleModal={this.props.handleModalAddSite}
          site={this.props.site}
          submit={this.props.handleSubmitSite}
          visible={this.props.visibleAddSite}
        />
        <ModalAddLocationType
          handleModal={this.props.handleModalAddLocationType}
          locationType={this.props.locationType}
          submit={this.props.handleSubmitLocationType}
          visible={this.props.visibleAddLocationType}
        />
        <Form layout="vertical" onSubmit={this.handleSubmit}>
          {/* <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="locations.add.subtitle.data" />
        </Divider> */}
          <div className="isoDetailDataFlex" style={{ padding: "0" }}>
            <div
              className="isoColumn-2 isoFormGroupComp"
              style={{ padding: "0 15px 0 0" }}
            >
              <InputGroup compact>
                <Col span={20}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="locations.add.code" />}
                    validateStatus={
                      error.location_code || getFieldError("location_code")
                        ? "error"
                        : null
                    }
                    help={
                      error.location_code
                        ? error.location_code
                        : getFieldError("location_code")
                    }
                  >
                    {getFieldDecorator("location_code", {
                      rules: [
                        {
                          max: 10,
                          message: <IntlMessages id="maxLength10" />
                        }
                      ]
                    })(
                      <Input disabled={this.state.check_code ? true : false} />
                    )}
                  </Form.Item>
                </Col>
                <Col span={4}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="autoGenerateCode" />}
                  >
                    <Checkbox
                      style={{
                        paddingTop: "5px",
                        paddingLeft: "8px"
                      }}
                      checked={this.state.check_code}
                      onChange={this.handleChangeCode}
                    />
                  </Form.Item>
                </Col>
              </InputGroup>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="locations.add.name" />}
              >
                {getFieldDecorator("location_name", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    },
                    {
                      max: 50,
                      message: <IntlMessages id="maxLength50" />
                    }
                  ]
                })(<Input />)}
              </Form.Item>

              <InputGroup>
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="locations.add.locationType" />}
                  >
                    {getFieldDecorator("id_location_type", {
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Select
                        loading={loading}
                        showSearch
                        optionFilterProp="children"
                        style={{ width: "100%" }}
                      >
                        {permissions.includes("Create Location Type") ? (
                          <Option
                            key={"Create Location Type"}
                            value={undefined}
                            onClick={() =>
                              this.props.handleModalAddLocationType()
                            }
                          >
                            <div
                              style={{ color: "#1890FF", fontStyle: "italic" }}
                            >
                              {" + " +
                                getLanguage({
                                  id: "locations.add.locationType"
                                })}
                            </div>
                          </Option>
                        ) : null}

                        {this.props.location.location_types.map((data, key) => (
                          <Option key={key} value={data.id_location_type}>
                            {data.location_type_name}
                          </Option>
                        ))}
                      </Select>
                    )}
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="locations.add.site" />}
                  >
                    {getFieldDecorator("id_site", {
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Select
                        loading={loading}
                        showSearch
                        optionFilterProp="children"
                        style={{ width: "100%" }}
                      >
                        {permissions.includes("Create Site") ? (
                          <Option
                            key={"Create Site"}
                            value={undefined}
                            onClick={() => this.props.handleModalAddSite()}
                          >
                            <div
                              style={{ color: "#1890FF", fontStyle: "italic" }}
                            >
                              {" + " +
                                getLanguage({ id: "locations.add.site" })}
                            </div>
                          </Option>
                        ) : null}

                        {this.props.location.sites.map((data, key) => (
                          <Option key={key} value={data.id_site}>
                            {data.site_name}
                          </Option>
                        ))}
                      </Select>
                    )}
                  </Form.Item>
                </Col>
              </InputGroup>

              <InputGroup>
                {/* <Col span={12}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="locations.add.costingMethod" />}
                >
                  {getFieldDecorator("costing_method", {
                    initialValue: 1,
                    rules: [
                      {
                        required: true,
                        message: <IntlMessages id="required" />
                      }
                    ]
                  })(
                    <Radio.Group buttonStyle="solid" style={radioGroupStyle}>
                      <Radio.Button style={radioButtonStyle} value={1}>
                        Average
                      </Radio.Button>
                      <Radio.Button style={radioButtonStyle} value={2}>FIFO</Radio.Button>
                    </Radio.Group>
                  )}
                </Form.Item>
              </Col> */}
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="locations.add.inventoryType" />}
                  >
                    {getFieldDecorator("inventory_type", {
                      initialValue: 1,
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Radio.Group buttonStyle="solid" style={radioGroupStyle}>
                        <Radio.Button style={radioButtonStyle} value={1}>
                          Normal
                        </Radio.Button>
                        {/* <Radio.Button style={radioButtonStyle} value={2}>Minus</Radio.Button> */}
                      </Radio.Group>
                    )}
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="locations.add.leadTime" />}
                  >
                    {getFieldDecorator("lead_time", {
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(<InputNumber min={0} style={{ width: "100%" }} />)}
                  </Form.Item>
                </Col>
              </InputGroup>

              <InputGroup>
                <Col span={24}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="locations.add.account" />}
                  >
                    {getFieldDecorator("id_account_item_transfer", {
                      rules: [
                        {
                          required: true,
                          message: <IntlMessages id="required" />
                        }
                      ]
                    })(
                      <Select
                        loading={loading}
                        showSearch
                        optionFilterProp="children"
                        style={{ width: "100%" }}
                      >
                        {this.props.location.accounts.map((data, key) => (
                          <Option key={key} value={data.id_account}>
                            {data.account_code + " (" + data.account_name + ")"}
                          </Option>
                        ))}
                      </Select>
                    )}
                  </Form.Item>
                </Col>
              </InputGroup>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="taxNumber" />}
              >
                {getFieldDecorator("tax_number", {
                  rules: [
                    {
                      max: 30,
                      message: <IntlMessages id="maxLength30" />
                    }
                  ]
                })(<Input allowClear={true} />)}
              </Form.Item>
            </div>
            <div
              className="isoColumn-2 isoFormGroupComp"
              style={{ padding: "0 0 0 15px" }}
            >
              <Form.Item
                {...formTextArea}
                label={<IntlMessages id="address" />}
              >
                {getFieldDecorator("address", {
                  rules: [
                    {
                      required: true,
                      message: <IntlMessages id="required" />
                    }
                  ]
                })(
                  <TextArea
                    autosize={{ minRows: 4 }}
                    style={{ marginBottom: "8px" }}
                  />
                )}
              </Form.Item>

              <InputGroup>
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="locations.add.email" />}
                  >
                    {getFieldDecorator("email", {
                      rules: [
                        {
                          type: "email",
                          message: <IntlMessages id="invalidEmail" />
                        }
                      ]
                    })(<Input allowClear={true} />)}
                  </Form.Item>
                </Col>
                <Col span={12}>
                  <Form.Item
                    {...formItemLayout}
                    label={<IntlMessages id="locations.add.phone" />}
                  >
                    {getFieldDecorator("phone", {
                      rules: [
                        {
                          max: 20,
                          message: <IntlMessages id="maxLength20" />
                        },
                        {
                          pattern: "^[0-9]*$",
                          message: <IntlMessages id="invalidPhone" />
                        }
                      ]
                    })(
                      <Input
                        //addonBefore="+62"
                        style={{ width: "100%" }}
                        allowClear={true}
                      />
                    )}
                  </Form.Item>
                </Col>
              </InputGroup>

              <Form.Item
                {...formItemLayout}
                label={<IntlMessages id="locations.add.url" />}
              >
                {getFieldDecorator("url", {})(<Input allowClear={true} />)}
              </Form.Item>

              <Form.Item
                {...formTextArea}
                label={<IntlMessages id="locations.add.description" />}
              >
                {getFieldDecorator(
                  "description",
                  {}
                )(<TextArea autosize={{ minRows: 4 }} />)}
              </Form.Item>
            </div>
          </div>

          <Form.Item {...tailFormItemLayout}>
            <Button
              type="primary"
              htmlType="submit"
              style={{ marginTop: "20px", marginLeft: "50px" }}
              loading={loading || loadingSubmit}
            >
              <IntlMessages id="button.submit" />
            </Button>
          </Form.Item>
        </Form>
      </>
    );
  }
}

const WrappedLocationCreateForm = Form.create({ name: "create_location" })(
  LocationCreateForm
);

export default WrappedLocationCreateForm;
